body {
    position:relative;
    font-family: 'Microsoft YaHei', sans-serif;
    min-height: 100vh;
    min-width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
}

.form_box {
    display: inline-block;
    min-width: 320px;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
    border-radius: 15px;
    padding: 30px;
    box-sizing: border-box;
}
.title {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
}
.form_input {
    position: relative;
    margin: 20px 0;
}
.form_input label {
    display: inline-block;
    margin-bottom: 5px;
    cursor: pointer;
}
.form_input.error_input label {
    color: red;
}
.form_input input {
    width: 100%;
    padding: 10px 20px;
    border: 1px solid grey;
    border-radius: 5px;
    box-sizing: border-box;
}
.form_input.error_input input {
    border-color: red;
}
.form_input .error_info {
    position: absolute;
    top: 100%;
    left: 0;
    color: red;
    font-size: 12px;
    margin-top: 2px;
    white-space: nowrap;
}
.form_box button {
    display: inline-block;
    width: 100%;
    padding: 20px 20px;
    margin: 15px 0 0;
    background: #01d6fe;
    border: none;
    border-radius: 5px;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    transition: background .5s;
}
.form_box button:hover {
    background: #333;
}
.form_box .success_info {
    padding: 20px 20px 0;
    text-align: center;
    color: #2E8B57;
}
